<template>
  <v-app>
    <SideBar/>
    <v-app-bar 
      app
      :color= "appBarColor"
      dark
      class="px-sm text-left shadow-sm"
      dense
      height="50"
    >
    <v-app-bar-nav-icon
        v-ripple="{class: 'primary--text'}"
        @click="toggleVerticalSidebarDrawer"
    />
      <v-divider class = "mx-2" vertical/>
      <v-spacer/>
      <v-app-bar-title v-if="isSelectedProfile"> {{ selectedTemplateName }}</v-app-bar-title>
      <v-app-bar-title v-else>
        <v-alert
          color = "red"
          class = "my-2"
          dense
          type= "error"
          > <strong> No profile selected </strong> 
        </v-alert>
      </v-app-bar-title>




      <v-icon class = "mx-4" large v-if="isReadOnly">mdi-pencil-off-outline</v-icon>
      <v-spacer/>
      <v-divider class = "mx-6" vertical/>
      <div class="d-flex align-right">
        <v-tooltip v-if="!$vuetify.theme.dark" bottom>
          <template v-slot:activator="{ on }">
            <v-btn v-on="on" :color="appBarColor" small fab @click="darkMode">
              <v-icon class="mr-1">mdi-moon-waxing-crescent</v-icon>
            </v-btn>
          </template>
          <span>Dark Mode On</span>
        </v-tooltip>
        <v-tooltip v-else bottom>
          <template v-slot:activator="{ on }">
            <v-btn v-on="on" :color="appBarColor" small fab @click="darkMode">
              <v-icon color="yellow">mdi-white-balance-sunny</v-icon>
            </v-btn>
          </template>
          <span>Dark Mode Off</span>
        </v-tooltip>
      </div>

    </v-app-bar>
    <v-main>
      <router-view/>
      <Footer/>
      
    </v-main>

    

  </v-app>
</template>

<script>
import SideBar from './components/Sidebar';
import Footer from './components/Footer';

import {mapActions} from 'vuex'

export default {
  name: 'App',
  components: {
    SideBar,
    Footer
  },

  data: () => ({
    profileName: "SELECT PROFILE",
    selectedTemplate: {"No profile selected": "Plese select"},
    color: 'white',
  }),
   computed:  {
      appBarColor: {
        get () {
          return this.$store.state.selectedTemplate.color ? this.$store.state.selectedTemplate.color : "primary"
        }
      },
      isReadOnly: {
        get () {
          return this.$store.state.selectedTemplate.readonly ? this.$store.state.selectedTemplate.readonly : false
        }
      },
      selectedTemplateName: {
        get () {
          return (this.$store.state.selectedTemplate.name) ? this.$store.state.selectedTemplate.name : "No profile selected"
        }
      },
      isSelectedProfile () {
        return (this.$store.state.selectedTemplate.name) ? true : false
    },


    },
    watch: {
      //
    },

    created () {
      //
    },
    mounted() {
      //
    },

  methods: {
    darkMode() {
      this.$vuetify.theme.dark = !this.$vuetify.theme.dark;
    },
    ...mapActions([
        'changeVerticalSidebarDrawer',
        'changeVerticalSidebarMini',
    ]),
    toggleVerticalSidebarDrawer() {
        this.changeVerticalSidebarDrawer()
    },
  }
}
</script>
<style>
  .v-tabs-slider-wrapper, .v-tab--active {
    color: #fff !important;
  }
</style>