import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,    
  },
  {
    path: '/Enviroments',
    name: 'Enviroments',
    component: () => import('../views/Enviroments.vue')
  },
  {
    path: '/Compare',
    name: 'Compare',
    component: () => import('../views/Compare.vue')
  },
  {
    name: "Live Server config",
    path: "/LiveServerConfig",
    component: () => import('../views/LiveServerConfig.vue')
  },
  {
    name: "Live Events",
    path: "/LiveEvents",
    component: () => import('../views/LiveEvents.vue')
  },
]

const router = new VueRouter({
  routes,
  base: '/app/',
  mode: 'history'
})

export default router
