import Vue from 'vue';
import Vuex from 'vuex';
import themeConfig from './modules/themeConfig'

Vue.use(Vuex);

export default new Vuex.Store({
  modules: {
    themeConfig,
  },

  state: {
    selectedTemplate: {"No profile selected": "Plese select"},
    trial: true,
    open: [],
  },

  getters: {
    selectedTemplate(state) {
      return state.selectedTemplate;
    },
    getSelectedOpen(state) {
      return state.open;
    },
  },

  mutations: {
    SET_TEMPLATE(state, payload){
      state.selectedTemplate = payload;
    },
    SET_OPEN(state, payload){
      state.open = payload;
    },
    
  },

  actions: {
    setTemplate(context, payload) {
      payload.password = "*****"
      context.commit("SET_TEMPLATE", payload);
    },
    setOpen(context, payload){
      context.commit("SET_OPEN", payload)
    }
  },
});
