<template>
  <v-navigation-drawer
    v-model="getThemeMode.verticalSidebarDrawer"
    :color="$vuetify.theme.dark ? 'dark' : ''"
    :expand-on-hover.sync="getThemeMode.verticalSidebarMini"
    :mini-variant="getThemeMode.verticalSidebarMini"
    :dark="getThemeMode.verticalSidebarDrawerColor != 'white' ? true : false"
    app
    :disable-resize-watcher="false"
    :mobile-breakpoint="960"
    height="100%"
    class="shadow-sm"
    :floating="true"
    v-bind="$attrs"
  >
    <vue-perfect-scrollbar
      :settings="{ suppressScrollX: true, wheelPropagation: false }"
      class="h-100 rtl-ps-none ps scroll"
      style="height: 100%"
    >
      <v-list dense nav>
        <v-list-item class="px-2">
            <v-img
            contain
            v-if = "!isDarkTheme"
            src = "../assets/endologo_transparent_dots-wrap.png"
            />
            <v-img
            contain
            v-if = "isDarkTheme"
            src = "../assets/endo-logo-inverted-wrap.png"
            />
        </v-list-item>
        </v-list>
        <v-divider/>
        <v-list dense nav>
        <v-list-item class="px-0">
          <v-tooltip bottom>
            <template v-slot:activator="{ on }">
              <v-icon  v-on="on" :color=color>mdi-help-circle-outline</v-icon>
            </template>
            <v-list dense nav>
              <v-list-item
                v-for="(value, key, index) in this.$store.state.selectedTemplate"
                :key="index">
                <div v-if="key != 'password' && key != 'zookeeper'">{{ key }} : {{ value }}</div>
                <div v-if="key == 'password'">{{ key }} : ***** </div>
                <div v-if="key == 'zookeeper'">{{ key }} : host: {{ value.host }} | username: {{value.username}} | password: ***** </div>
              </v-list-item>
            </v-list>
          </v-tooltip>
          <v-spacer/>
          <v-scroll-x-transition>
            <v-btn icon @click.stop="toggleSidebar">
              <v-tooltip
                v-if="!getThemeMode.verticalSidebarMini"
                right
                color="primary"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-icon dark v-bind="attrs" v-on="on" color
                    >mdi-circle-slice-8</v-icon
                  >
                </template>
                <span>UnPin</span>
              </v-tooltip>
              <v-tooltip
                v-if="getThemeMode.verticalSidebarMini"
                right
                color="primary"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-icon dark v-bind="attrs" v-on="on" color
                    >mdi-circle-outline</v-icon
                  >
                </template>
                <span>pin</span>
              </v-tooltip>
            </v-btn>
          </v-scroll-x-transition>
        </v-list-item>
      </v-list>
      <v-divider/>
      <v-list dense nav>
        <v-select
            :color="getColor"
            class="pt-6 px-2"
            v-model="profileInputDataComp"
            item-text = "name"
            item-value = "value"
            :items="profileInputData"
            label="Select Profile"
            return-object
            solo
            outlined
            dense
        ></v-select>
      </v-list>
      <v-divider/>
      <v-list dense nav>
        <template v-for="(item, i) in computedItems">
          <div
            :key="item.subheader"
            v-if="item.subheader"
            :class="miniVariant && 'px-1'"
          >
            <v-subheader>
              {{ item.subheader }}
            </v-subheader>
            <v-divider/>
          </div>
          <base-item
            v-else-if="!item.subheader && !item.children"
            :key="`item-${i}`"
            :item="item"
          />
        </template>
      </v-list>
    </vue-perfect-scrollbar>
  </v-navigation-drawer>
</template>

<script>
import ProfilesDataService from "../services/ProfilesDataService";
import { mapGetters, mapActions } from "vuex";
import { items } from "../data/navigation";
import VuePerfectScrollbar from "vue-perfect-scrollbar";
import baseItem from "./base/item.vue"
//import baseItemGroup from "./base/itemGroup.vue"

export default {

  data: () => ({
    color: '',
    profileInputData: [],
    miniVariant: false,
    expandOnHover: false,
    items: items,
    opacity: 0.4,
    profileName: "SELECT PROFILE",
  }),
  components: {
    VuePerfectScrollbar,
    baseItem,
    //baseItemGroup,
  },
  computed: {
    ...mapGetters(["getThemeMode"]),
    getMiniVariant() {
      return this.miniVariant;
    },
    computedItems() {
      return this.items.map(this.mapItem);
    },
    isDarkTheme() {
      return (this.$vuetify.theme.isDark)
    },
    getColor () {
      return (this.$store.state.selectedTemplate.color) ? this.$store.state.selectedTemplate.color : ''
    },

    profileInputDataComp: {
      get () {
        return this.profileInputData
      },
      set (newVal) {
        this.profileName = newVal.name;      
        this.$store.dispatch('setTemplate', newVal)
        this.color = this.$store.state.selectedTemplate.color ? this.$store.state.selectedTemplate.color : "primary"
        return this.profileInputData
      }
    }
  },
  watch: {
    //
  },
  created () {
    //
  },
  mounted() {
    this.getAllProfileData ()
  },

  methods: {
    ...mapActions(["changeVerticalSidebarDrawer", "changeVerticalSidebarMini"]),
    toggleSidebar() {
      this.changeVerticalSidebarMini();
      this.expandOnHover = !this.expandOnHover;
    },
    darkMode() {
      this.$vuetify.theme.dark = !this.$vuetify.theme.dark;
    },

    mapItem(item) {
      return {
        ...item,
        children: item.children ? item.children.map(this.mapItem) : undefined,
        title: item.title
      };
    },

    dataTranform (input) {
        var result = [];
        for (let parent in input) {
          input[parent].name = parent  
          result.push(input[parent]);
        }
        return result
      },

    async getAllProfileData () {
      try {
        const response = await ProfilesDataService.getAll()
        this.profileInputData = this.dataTranform(response.data)
      } catch(e) {
        console.log(e);
      }
    },

  }
};
</script>

<style lang="scss">
.ps-container {
  position: inherit !important;
}
.app-admin-wrap-layout-1.sidebar-mini {
  .v-toolbar {
    left: 56px !important;
    transition: all 0.5s cubic-bezier(0.685, 0.0473, 0.346, 1);
  }
  .v-main {
    padding: 75px 0px 12px 56px !important;
    transition: all 0.5s cubic-bezier(0.685, 0.0473, 0.346, 1);
    padding-bottom: 0px !important;
  }
  &.sidebar-close {
    .v-toolbar {
      left: 0px !important;
      transition: all 0.5s cubic-bezier(0.685, 0.0473, 0.346, 1);
    }
    .v-main {
      padding: 75px 0px 12px 0px !important;
      transition: all 0.5s cubic-bezier(0.685, 0.0473, 0.346, 1);
      padding-bottom: 0px !important;
    }
  }
}
</style>
